.same__navbar{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.same__navbar-logo{
  display: flex;
  flex-direction: row;
  gap: 10px;
  justify-content: flex-start;
  align-items: center;
}
.same__navbar-logo a > img{
  width: 150px;
}
.same__navbar-logo input{
  width: 200px;
  height: 30px;
  border-radius: 5px;
  border: black 1px solid;
}
.same_navbar-logo__switch button{
  position: relative;
  right: 35px;
  border: none;
  background: none;
}
.search{
  z-index: 10;
  position: relative;
  right: 40px;
}
.off{
  display: none;
}

.sticky-nav {
  position: sticky;
  top: 0;
}

.same__navbar-links{
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.same__navbar-links a{
  text-decoration: none;
  color: black;
  font-size: 18px;
  font-weight: 500;
}
